import { Injectable } from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {Observable} from "rxjs";

export class Heroes {
  constructor(public id:number, public name:string, public power: string, public weakness: string, public info: string){}
}

@Injectable({
  providedIn: 'root'
})

export class HttpClientService {
  baseUrl = "https://alheroapp.herokuapp.com/";
  constructor(private httpClient: HttpClient) {}


  sendLoginInfo(username, password){
    let cred = [];
    cred[0] = window.btoa(username);
    cred[1] = window.btoa(password);
    return this.httpClient.put(this.baseUrl + 'hero/pass-cred', cred);
  }

  responseToJava(data: number){
    return this.httpClient.post(this.baseUrl + 'send-to-java/', data.toString());
  }

  responseFromJava():Observable<string>{
    const requestOptions: Object = {responseType: 'text'}
    return this.httpClient.get<string>(this.baseUrl + 'send-to-ang', requestOptions);
  }

  createHeroes(hero){
    return this.httpClient.post<Heroes>(this.baseUrl + 'add-hero', hero);
  }

  getAllHeroes():Observable<any>{
    return this.httpClient.get(this.baseUrl + 'hero');
  }

  editHero(hero, id){
    return this.httpClient.put<Heroes>(this.baseUrl + `edit-hero/${id}`, hero);
  }

  deleteHero(hero, id){
    return this.httpClient.delete<Heroes>(this.baseUrl + 'delete-hero/' + id,{});
  }
}
